code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body > #root > div {
  height: 100%;
  font-family: 'Source Sans Pro', sans-serif;
}

.App {
  position: absolute;
  background-color: rgb(255, 255, 255);
  height: 100% !important;
}

